<template>
  <div id="SkuControl">
    <div class="OptGroup">
      <div class="queryBlock">
        <span>sku</span>
        <el-input v-model="sku"></el-input>
        <el-button @click="queryFn" type="primary">查询</el-button>
      </div>
      <div class="downloadTemplate">
        <a :href="_url2 + 'static/data/海外仓sku对照表_模板.xlsx'" download=""
          >下载模板</a
        >
      </div>
      <div class="uploadFile">
        <input
          style="width:200px"
          type="file"
          ref="uploadInput"
          @change="changeFile"
          value="文件上传"
          accept=".xls, .xlsx"
        />
        <el-button @click="submitUpload" type="primary">确认上传</el-button>
      </div>
    </div>
    <div class="table_container">
      <el-table max-height="720" border :data="tableData1">
        <el-table-column align="center" prop="0" label="海外仓sku" width="300">
        </el-table-column>
        <el-table-column
          align="center"
          prop="1"
          label="亚马逊站点sku"
          width="300"
        >
        </el-table-column>
        <el-table-column>
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="editRow(scope.row)"
              >修改</el-button
            >
            <el-button type="text" size="small" @click="deleteRow(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-table max-height="720" border :data="tableData2">
        <el-table-column align="center" prop="0" label="海外仓sku" width="300">
        </el-table-column>
        <el-table-column
          align="center"
          prop="1"
          label="亚马逊站点sku"
          width="300"
        >
        </el-table-column>
        <el-table-column>
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="editRow(scope.row)"
              >修改</el-button
            >
            <el-button type="text" size="small" @click="deleteRow(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      title="跟新海外仓对照sku"
      :visible.sync="dialogVisible"
      width="30%"
    >
      <div style="display:flex">
        <span
          style="width: 100px;
   
    line-height: 40px;"
          >请输入sku:</span
        ><el-input v-model="yy_sku1"></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitEdit">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="确认删除？" :visible.sync="dialogVisible1" width="30%">
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitDelete">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogVisible: false,
      dialogVisible1: false,
      tableData: [],

      sku: "",
      fd: "",
      yy_sku: "",
      an_sku: "",
      yy_sku1: ""
    };
  },
  computed: {
    tableData1() {
      return this.tableData.filter((item, index) => {
        return index % 2 === 0;
      });
    },
    tableData2() {
      return this.tableData.filter((item, index) => {
        return index % 2 === 1;
      });
    }
  },
  methods: {
    submitDelete() {
      let url =
        this._url2 + "server/order_management/oversea_location_sku_delete/";
      let fd = new FormData();
      fd.append("yy_sku", this.yy_sku);
      fd.append("an_sku", this.an_sku);
      this.$axios.post(url, fd).then(res => {
        if (res.data.code === 200) {
          this.$message.success(res.data.msg);
        } else {
          this.$message.warnging("删除请求失败");
        }
      });
    },
    submitEdit() {
      if (!this.yy_sku1) {
        this.$message.warnging("请输入sku");
        return;
      }
      let url =
        this._url2 + "server/order_management/oversea_location_sku_update/";
      let fd = new FormData();
      fd.append("yy_sku", this.yy_sku);
      fd.append("an_sku", this.an_sku);
      fd.append("yy_sku1", this.yy_sku1);
      this.$axios.post(url, fd).then(res => {
        if (res.data.code === 200) {
          this.$message.success(res.data.msg);
          this.yy_sku1 = "";
          this.dialogVisible = false;
        } else {
          this.$message.warnging("请求失败");
        }
      });
    },
    editRow(rowData) {
      this.dialogVisible = true;
      console.log(rowData);
      this.yy_sku = rowData[0];
      this.an_sku = rowData[1];
    },
    deleteRow(rowData) {
      this.dialogVisible1 = true;
      console.log(rowData);
      this.yy_sku = rowData[0];
      this.an_sku = rowData[1];
    },
    queryData(sku) {
      let url = "";
      if (sku && sku.length !== 0) {
        url =
          this._url2 +
          "server/order_management/oversea_location_sku_contrast_detail/?sku=" +
          sku;
      } else {
        url =
          this._url2 +
          "server/order_management/oversea_location_sku_contrast_detail/";
      }

      this.$axios.get(url).then(res => {
        console.log(res);
        if (res.data.code === 200) {
          this.tableData = res.data.data;
        } else {
          alert("获取数据错误");
        }
      });
    },
    submitUpload() {
      console.log(111);
      if (!this.files) {
        alert("请先上传文件");
        return;
      }
      let url =
        this._url2 +
        "server/order_management/oversea_location_sku_contrast_upload/";
      let fd = new FormData();
      fd.append("files", this.files);
      this.$axios.post(url, fd).then(res => {
        if (res.data.code === 200) {
          alert("上传成功");
          this.files = "";
          this.$refs.uploadInput.value = "";
        } else {
          alert("上传失败");
        }
      });
    },
    changeFile() {
      this.files = this.$refs.uploadInput.files[0];

      if (this.files.name) {
        console.log(this.files);
        let hz = this.files.name.split(".")[1];
        console.log(hz);
        if (hz !== "xls" && hz !== "xlsx") {
          this.$refs.uploadInput.files = null;
          alert("请上传表格文件");
          return;
        }
      }
    },
    queryFn() {
      if (this.sku === "") {
        alert("请输入sku查询！");
        return;
      }
      this.queryData(this.sku);
    }
  },
  created() {
    this.queryData();
  }
};
</script>

<style scope>
#SkuControl .OptGroup {
  display: flex;
  justify-content: space-around;
  padding: 22px 0;
  width: 60vw;
  margin: 10px auto;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 5px;
}
#SkuControl .table_container {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  width: 74vw;
}
#SkuControl .queryBlock {
  display: flex;
  align-items: center;
}
#SkuControl .queryBlock > span,
#SkuControl .queryBlock > .el-input {
  margin-right: 20px;
}
.downloadTemplate {
  display: flex;
  align-items: center;
  text-align: center;
  width: 100px;
}
#SkuControl .downloadTemplate > a {
  width: 200px;
  background-color: #409eff;
  border-radius: 4px;
  color: #fff;
  font-size: 14px;
  padding: 10px;
}
#SkuControl .el-table {
  width: 711px;
  margin: 0 auto;
  overflow-x: hidden;
}
</style>



